@import url('../../index.css');

.data-table-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
}

.table-filters {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    padding: 0 8px;
    z-index: 2;
}

.filter-item {
    /* flex: 1; */
    min-width: 200px;
}

.table-container {
    height: 640px;
    overflow: auto;
    position: relative;
}

table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 12px;
    /* position: relative; */
}

thead tr {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: var(--bg-color);
}

th {
    padding: 12px;
    text-align: left;
    position: relative;
    color: var(--text-color);
    font-weight: 500;
    font-size: 14px;
}

.header-clickable {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    user-select: none;
}

.header-clickable:hover {
    color: var(--secondary-color);
}

.sort-icon {
    width: 16px;
    height: 16px;
}

.table-row td {
    background-color: var(--card-color);
    padding: 12px;
    color: var(--title-color);
    font-size: 14px;
    font-weight: 500;
    max-width: 240px;
}

.table-row td:first-child {
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
}

.table-row td:last-child {
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
}

.highlight-matches {
    background-color: var(--tertiary-color);
    padding: 0 2px;
    color: var(--primary-color);
}

